import { Component } from "@angular/core";
import { LoadingController, NavController } from "ionic-angular";

import { ListTaskComponent } from "../listTask/listTask";
import { MyListTaskComponent } from "../myListTask/myListTask";
// import { SearchComponent } from '../search/search';
import { CreateTaskComponent } from "../createTask/createTask";
import { HelpComponent } from "../help/help";
// import { SettingsComponent } from '../settings/settings';

@Component({
  selector: "page-home",
  templateUrl: "home.html"
})
export class HomeComponent {
  public pages: Array<{ title: string; component: any }>;

  constructor(
    public navCtrl: NavController,
    public loadingCtrl: LoadingController
  ) {
    this.pages = [
      { title: " ", component: ListTaskComponent },
      { title: "  ", component: MyListTaskComponent },
      { title: " ", component: CreateTaskComponent },
      // { title: ' ', component: SearchComponent },
      // { title: '', component: SettingsComponent },
      { title: "", component: HelpComponent }
    ];
  }

  public openPage(page) {
    const loader = this.loadingCtrl.create({
      content: " ..."
    });
    loader.present();
    this.navCtrl.setRoot(page.component, null, null, () => {
      loader.dismiss();
    });
  }
}



